import bg from './../image/bg.png'

function ExImage(props){
   
    return(
    <div className="container">
        <div className="main-bg" style={{ backgroundImage : 'url(' + bg + ')' }}></div>   
        <h4>public 폴더에 있는 이미지 사용할 땐</h4>
        <img src="/logo192.png" /> 
        <h6>public 폴더에 있는 이미지 사용할 땐(권장)</h6>
        <img src={process.env.PUBLIC_URL + '/logo192.png'} /> 
        <h6>
        *codingapple.com/어쩌구/ 경로에 배포하면 /어쩌구/ 를 뜻하는 process.env.PUBLIC_URL 이것도 더해주면 됨.
        </h6>
        <h6>
        *codingapple.com/어쩌구/ 경로에 리액트로 만든 페이지를 배포할 일이 아예 없으면 굳이 안해도 됩니다. 
        </h6>
    </div> 
    )
}


export default ExImage;